body {
  display: block;
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}
.page-header {
  width: 320px;
  padding: 0;
  margin: 0 auto;
}
.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 260px;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-weight: normal;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensans.woff2") format("woff2"), url("fonts/opensans.woff") format("woff");
}
@font-face {
  font-weight: bold;
  font-family: "Open Sans";
  font-style: normal;
  src: url("fonts/opensansbold.woff2") format("woff2"), url("fonts/opensansbold.woff") format("woff");
}
@media (min-width: 768px) {
  .page-header {
    width: 768px;
  }
  .container {
    padding: 0 50px;
    width: calc(768px - 100px);
  }
}
@media (min-width: 1150px) {
  .page-header {
    width: 1150px;
  }
  .container {
    padding: 0 50px;
    width: calc(1150px - 100px);
  }
}
.page-header {
  display: flex;
  flex-direction: column;
  border-top: 1px solid #ececec;
  border-bottom: 1px solid #ececec;
}
.page-header__logo {
  padding: 20px 0px;
  display: flex;
}
.page-header__logo picture {
  margin: auto;
}
.main-nav--closed .site-list,
.main-nav--closed .user-list {
  display: none;
}
.main-nav--nojs,
.main-nav--opened {
  display: block;
}
.nav-toggle {
  display: flex;
  border: none;
  background: #ffffff;
  padding: 30px 30px 50px;
  height: 14px;
  margin: -80px 0 0 240px;
}
.main-nav--closed .nav-toggle::before {
  display: block;
  content: "";
  width: 20px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav--opened .nav-toggle {
  padding: 40px 25px;
}
.main-nav--opened .nav-toggle::before,
.main-nav--opened .nav-toggle::after {
  content: "";
  width: 30px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .nav-toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .nav-toggle::after {
  margin: 0 0 0 -30px;
  transform: rotate(-45deg);
}
.main-nav--nojs .nav-toggle {
  display: none;
}
.main-nav--closed .page-header__logo,
.main-nav--closed .page-header__nav-toggle {
  display: flex;
}
.main-nav__wrapper {
  display: flex;
  flex-direction: column;
}
.site-list,
.user-list,
.offer-list {
  margin: 0px;
  padding: 0px;
  list-style: none;
}
.nav-note {
  font-size: 15px;
  line-height: 24px;
  font-weight: normal;
  color: #444444;
}
.offer-list,
.nav-note {
  display: none;
}
.site-list__item,
.user-list__item {
  border-top: 1px solid #ececec;
}
.site-list__item a,
.user-list__item a,
.offer-list__item a {
  display: flex;
  padding: 25px 0 28px 75px;
  text-decoration: none;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  color: #000000;
  cursor: pointer;
}
.user-list__item path {
  fill: #d3d2d2;
}
.user-list__search svg {
  margin-left: -42px;
  margin-top: 7px;
  position: absolute;
}
.user-list__cart svg {
  margin-left: -44px;
  margin-top: 5px;
  position: absolute;
}
@media (min-width: 768px) {
  .main-nav {
    padding: 0 50px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
  }
  .page-header__logo {
    padding: 0;
    margin: 22px 0 0;
  }
  .page-header {
    flex-direction: row;
  }
  .main-nav .nav-toggle {
    display: none;
  }
  .main-nav .site-list,
  .main-nav .user-list {
    display: flex;
    flex-direction: row;
    flex-grow: 0;
  }
  .user-list {
    order: 1;
    flex-grow: 1;
  }
  .site-list {
    order: 2;
  }
  .user-list span {
    display: none;
  }
  .site-list__item,
  .user-list__item {
    border: none;
  }
  .site-list__item a {
    padding: 0;
    margin: 25px 40px 25px 0;
    font-size: 15px;
  }
  .nav-note {
    order: 3;
    display: flex;
  }
}
@media (min-width: 1150px) {
  .site-list {
    order: 1;
  }
  .page-header__logo {
    order: 2;
    margin: 10px auto;
  }
  .user-list {
    order: 3;
  }
  .offer-list {
    order: 4;
    display: flex;
  }
  .nav-note {
    order: 5;
  }
  .offer-list a {
    padding: 0;
    margin: 25px 60px 25px 0;
    font-size: 15px;
  }
}
